// utility.js

// String(: Boolean)
is_string = (target) => {
    if(typeof target === 'string') return true;
    else return false;
}

// Array(: Boolean)
is_array = (target) => {
    if(Array.isArray(target)) return true;
    else return false;
}

// (1: (String/Array), 2: 
// (: / => Array, id => )
// document.querySelector()
// : elm_name
get_element = (elm_name, element=null) => {
    // (Array)
    if (is_array(elm_name)) {
        let elm_array = []
        elm_name.map(en => {
            let elms = (element !== null) ?
            Array.from(element.querySelectorAll(en))
            : Array.from(document.querySelectorAll(en));
            if (elms[0]) {
                // id[0]
                if (en.split(' ').slice(-1)[0].slice(0, 1) === '#') elm_array.push(elms[0]);
                // class
                else elm_array.push(...elms);
            }
        })
        return elm_array;
    // (String)
    } else {
        let elms = (element !== null) ? 
        Array.from(element.querySelectorAll(elm_name))
        : Array.from(document.querySelectorAll(elm_name));
        return (elm_name.split(" ").slice(-1)[0].slice(0, 1) === '#') ? 
            elms[0] : elms;
    }
}

// HTMLElement
// obj instanceof HTMLElement
// HTMLElement
//   -  'object'
//   - nodeType 1 (ELEMENT_NODE)
//   - style 'object'
//   - ownerDocument 'object'
isElement = (obj) => {
    try {
        //Using W3 DOM2 (works for FF, Opera and Chrom)
        return obj instanceof HTMLElement;
    }
    catch(e){
        //Browsers not supporting W3 DOM2 don't have HTMLElement and
        //an exception is thrown and we end up here. Testing some
         //properties that all elements have. (works on IE7)
        return (typeof obj==='object') &&
            (obj.nodeType===1) && (typeof obj.style === 'object') &&
            (typeof obj.ownerDocument ==='object');
    }
}