// preload.js
// UI

// (.preload)(Chrom)
remove_preload = () => {
    let preload = document.querySelectorAll('.u-preload');
    preload.forEach( (pre) => {
        setTimeout((e) => { // 1(1)
            pre.classList.remove('u-preload');
        }, 1000);
    })
}

window.addEventListener('load', () => {
    remove_preload();
});