/* toggle.js ----------------------------------
|   On/OffUI   
|   Rules :
|   - .toggler
|   - UI.togglerdata(data-status) On / Off 
|   - .togglerTGL_TARGET_LIST(dist/ignored/js/variable.js)
|   - target_list(1) => key(id) : [(string), ...]
|   - target_list(2) => key(id) : [[*1], [*2]]
|     - *1 => togglerdata-status(: toggler(on) => on)
|     - *2 => togglerdata-status(: toggler(on) => off)
---------------------------------------------*/

// togglerdata-status(toggler: target, : anti-target)
// js( : modal.js, scroll.js, file.js)
toggle_target_data = (toggler, target, anti_target=null) => {
    if (anti_target !== null) {
        anti_target.map(anti => {
            if (anti !== null) anti.dataset.status = (toggler.dataset.status !== 'on') ? 'off' : 'on';
        })
    }
    toggler.dataset.status = (toggler.dataset.status !== 'on') ? 'on' : 'off';
    if (target[0]) target.map(trg => {
        if (trg !== null) trg.dataset.status = toggler.dataset.status;
    })
}

// .toggler TGL_TARGET_LIST
// => .toggler data-status on / off 
// ( : ajax.js ... toggle)
toggle = (toggler=null) => {
    let togglers =
        (toggler) ? 
            (is_array(toggler)) ?
                toggler : [toggler]
        : get_element('.toggler');
    if (!togglers[0]) return [];
    togglers.map(tgl => {
        tgl.addEventListener('click', () => {
            let target = null; // togglerdata-status
            let anti_target = null; // togglerdata-status

            // togglerid (togglerTGL_TARGET_LISTid)
            if (typeof TGL_TARGET_LIST[tgl.id] !== 'undefined') {
                // (anti_target)
                if (is_array(TGL_TARGET_LIST[tgl.id][0])) {
                    target = get_element(TGL_TARGET_LIST[tgl.id][0]);
                    anti_target = get_element(TGL_TARGET_LIST[tgl.id][1]);
                // (target)
                } else { target = get_element(TGL_TARGET_LIST[tgl.id]);}

            // toggler ( : tgl-reactor)
            } else if (get_element(".tgl-reactor", tgl.parentNode)[0]) {
                target = get_element(".tgl-reactor", tgl.parentNode);


            /*  ------------
            |    1 (p-entry-func)
            |     Parent >
            |       modal-opener
            |       modal > modal-closer
            |    2 (p-index-server)
            |     GrandParent >
            |       Parent > modal-opener
            |       modal > modal-closer
            |    3 (p-in_server)
            |      each located in random layers (opener)
            |       modal-opener (data-target_id = #modal-closer--)
            |                      (get_element # )
            |       modal > modal-closer
            |
            |   * modal-closer => modalopenerOff
            -----------------------------------------------------*/
            } else if (tgl.classList.contains('modal-opener')
            || tgl.classList.contains('modal-closer')) {
                target = [];
                target.push(OVERLAY, BODY);
                // Modal Opener 
                if (tgl.classList.contains('modal-opener')) {
                    let parent = tgl.parentNode;
                    let grand_parent = tgl.parentNode.parentNode;
                    //  1 
                    if (get_element('.modal', parent)[0]) {
                        target.push(
                            ...get_element('.modal', parent),
                            ...get_element(".modal .modal-closer", parent)
                        );
                    //  2 
                    } else if (get_element('.modal', grand_parent)[0]) {
                        target.push(
                            ...get_element('.modal', grand_parent),
                            ...get_element(".modal .modal-closer", grand_parent)
                        );
                    //  3 
                    } else if (typeof tgl.dataset.target_id != 'undefined') {
                        target.push(
                            get_element(tgl.dataset.target_id),
                            get_element(tgl.dataset.target_id).parentNode
                        )
                    }
                // Modal Closer 
                } else {
                    target.push(
                        ...get_element('.modal'),
                        ...get_element(".modal-opener"),
                    );  
                }

            // fixed-section
            } else if (tgl.classList.contains('fixed-section-closer')) {
                let section_id = tgl.id.split('closer--').slice(-1)[0];
                console.log(tgl.id.split('fixed-section-closer--')[0])
                target = [get_element(`#${ section_id }`)];
            }

            // tgldata-status(on / off)targetdata-status
            if (target !== null) toggle_target_data(tgl, target)
            else if (anti_target !== null) toggle_target_data(tgl, target, anti_target);
        })
    })
}

toggle();



// modal-closeridmodal (hmtlonclick) (js : file.js) 
open_modal_with_closer_id = (type) => {
    let tgl = get_element(`#modal-closer--${type}`);
    if (tgl) {
        target = [OVERLAY, BODY, tgl.parentNode];
        toggle_target_data(tgl, target);
    }
}

// fixed-section-closeridfixed-section (hmtlonclick)
open_fixed_section_by_section_id = (section_id) => {
    let tgl = get_element(`#fixed-section-closer--${ section_id }`);
    if (tgl) {
        target = [get_element(`#${ section_id }`)];
        toggle_target_data(tgl, target);
    }
}