//  (hmtlonclick)
prefill_server_update = (server_board) => {
    let id = server_board.dataset.id;
    let base64 = (get_element('img', server_board)[0]) ? get_element('img', server_board)[0].src : null;
    let server_name = get_element('.p-index__server-title', server_board)[0].textContent;

    get_element('#update-server-form').dataset.id = id;
    get_element("#update-server--name").value = server_name;
    get_element('#update-server--image').value = '';
    get_element('#update-server--image').disabled = false;
    get_element("#update-server-form .p-form__filename")[0].textContent = 'No file chosen';
    if (base64) {
        get_element('#update-server--image-wrap').innerHTML = `<img src='${ base64 }'>`;
        get_element('#update-server--delete-image-wrap').innerHTML = 
            `<input class="p-form__checkbox p-form__disabled-trigger" id='update-server--delete-image' name="delete_image" type="checkbox" value="y" onchange="checkbox_disable_input(this)">
            <label class="p-form__checkbox-label" for='update-server--delete-image'>DELETE IMAGE</label>`;
    } else { 
        get_element('#update-server--image-wrap').innerHTML = `<i class='fa-solid fa-image'></i>`; 
        if (get_element('#update-server--delete-image')) get_element('#update-server--delete-image-wrap').innerHTML = '';
    }
    get_element('#update-server--delete').dataset.confirm_txt = ` ${ server_name } `;
    get_element('#update-server--delete').dataset.server_id = id;
    get_element('#error-msg--update-server-name').textContent = '';
    get_element('#update-server-form .p-form__image-error-msg')[0].textContent = '';
}


// channel (hmtlonclick)
prefill_channel_update = (chnl_wrap) => {
    let id = chnl_wrap.dataset.chnl_id;
    let channel_name = get_element('.channel-name', chnl_wrap)[0].textContent.trim();

    get_element('#update-channel-form').dataset.id = id;
    get_element("#update-channel--name").value = channel_name;

    get_element('#update-channel--delete').dataset.confirm_txt = ` ${ channel_name } `;
    get_element('#update-channel--delete').dataset.chnl_id = id;
    get_element('#error-msg--update-channel-name').textContent = '';
}


