// file.py


const ALLOWED_IMAGE_FORMAT = ['jpg', 'jpeg', 'png']
const ALLOWED_FILE_FORMAT = ['jpg', 'jpeg', 'png', 'gif', 'pdf', 'txt']


// 
get_file_format = (filename) => {
    return format = filename.split('.').slice(-1)[0].toLowerCase();
}

// 
validate_format = (file, allowed_format) => {
    // 
    if (Object.prototype.toString.call(file).slice(8, -1) == 'FileList') {
        for(let i = 0; i < file.length; i++){
            let format = get_file_format(file[i].name);
            if (!allowed_format.includes(format)) return false
        }
        return true;
    } else {
        let format = get_file_format(file.name);
        return (allowed_format.includes(format));
    }
}

// 
// () => 5MB
// () => 25MB
validate_size = (file, second_files=null) => {
    try {
        // 
        if (Object.prototype.toString.call(file).slice(8, -1) == 'FileList') {
            let file_size = 0;
            for(let i = 0; i < file.length; i++) file_size += file[i].size;
            // +
            if (second_files) {
                for(let i = 0; i < second_files.length; i++){
                    file_size += second_files[i].size;
                }
            }
            return (file_size < 20000000);
        //
        } else { return (file.size < 5000000); }
    }
    catch (e) { return false; }
}





/* -----------------------------------------------
|   preview                 |
|   (profile)                              |
|    =>     |
------------------------------------------------*/
const FILE_ELM = get_element('.image-input');
const FILENAME_ELM = [];
FILE_ELM.map(fe => {
    FILENAME_ELM.push( get_element('.p-form__filename', fe.parentNode)[0] )
})
const IMAGE_ERROR_MSG = get_element('.p-form__image-error-msg');
const IMAGE_WRAP_ELM_NAME = '.image-wrap';
const PREVIEW_ELM = get_element(IMAGE_WRAP_ELM_NAME);

// 
const USER_IMAGE = get_element('.p-index-main__user-image')[0];
const USER_IMAGE_DELETE = get_element('#update-account--image-delete');

update_image = (file, file_index) => {
    let file_reader = new FileReader();
    // onload(event : ProgressEvent )
    file_reader.onload = (event) => {
        // event.currentTarget.resultbase64
        let base64_text = event.currentTarget.result
        let img = document.createElement('img');
        img.src = base64_text;
        // 
        PREVIEW_ELM[file_index].appendChild(img);
    }
    //base64
    file_reader.readAsDataURL(file)
}

clear_image = (file_index) => {
    PREVIEW_ELM[file_index].innerHTML = '';
}

update_image_form = () => {
    FILE_ELM.map(fe => {
        fe.addEventListener('change', () => {
            let file_index = FILE_ELM.indexOf(fe);
            clear_image(file_index);
            // 
            if (fe.files[0] && validate_format(fe.files[0], ALLOWED_IMAGE_FORMAT)) {
                let file = fe.files[0];
                if (FILENAME_ELM[file_index]) FILENAME_ELM[file_index].textContent = file.name;
                update_image(file, file_index);
                IMAGE_ERROR_MSG[file_index].textContent = '';
                USER_IMAGE_DELETE.value = '';
            } else {
                // file
                if (FILENAME_ELM[file_index]) FILENAME_ELM[file_index].textContent = 'No file chosen';
                // 
                if (fe.id == 'update-account--image') {
                    PREVIEW_ELM[file_index].innerHTML = `<i class='fa-solid fa-user'></i>`;
                    // 
                    if (get_element('img', USER_IMAGE)[0]) USER_IMAGE_DELETE.value = true;
                }
                // 
                if (fe.files[0]) IMAGE_ERROR_MSG[file_index].textContent = 'jpg / jpeg / png ';
            }
        })
    })
}
if (FILE_ELM) update_image_form();



// file input
const FORMS_WITH_SOLE_PREVIEW = get_element(['#update-account--image-form']);
const IMAGE_DELETE = get_element(['#update-account--image-delete']);

FORMS_WITH_SOLE_PREVIEW.map(fwsp => {
    let preview_clearer = get_element('.p-form__clear-circle-preview', fwsp)[0];
    let img_wrap = get_element('.image-wrap', fwsp)[0];
    let img_input = get_element('.image-input', fwsp)[0];
    let img_delete = IMAGE_DELETE[FORMS_WITH_SOLE_PREVIEW.indexOf(fwsp)];
    preview_clearer.addEventListener('click', () => {
        img_wrap.innerHTML = `<i class='fa-solid fa-user'></i>`;
        img_input.value = '';
        if (get_element('img', USER_IMAGE)[0]) img_delete.value = true;
    })
})





