// preview
const PREVIEW_LIST = get_element('.p-form__preview-list')[0];

// preview
insert_file_preview = (file) => {
    let file_reader = new FileReader();
    file_reader.onload = (event) => {
        let base64_text = event.currentTarget.result
        let random = Math.floor( Math.random() * 10000000 );
        let file_format = get_file_format(file.name);
        let thumnail_tag = '';
        if (validate_format(file, ALLOWED_IMAGE_FORMAT) || file_format == 'gif') {
            thumnail_tag = `<img src="${base64_text}" alt=""> `
        } else if (file_format == 'pdf') {
            thumnail_tag = `<i class="fa-regular fa-file-pdf"></i>`
        } else if (file_format == 'txt') {
            thumnail_tag = `<i class="fa-solid fa-file-signature"></i>`
        }
        html =
            `<div class="p-form__file-block c-menu-wrap" id="preview-${random}">
                <div class="p-form__image-wrap">${thumnail_tag}</div>
                <div class="p-form__filename">${file.name}</div>
                <div class="p-form__file-menu c-corner-icon-menu">
                    <button class="p-form__file-dumper c-menu-elm" data-id="preview-${random}" onclick="dump_file_preview(this)">
                        <i class="fa-solid fa-trash-can"></i>
                    </button>
                </div>
            </div>`;
        let li_element = document.createElement('li');
        li_element.innerHTML = html;
        // 
        PREVIEW_LIST.appendChild(li_element);
    }
    //base64
    file_reader.readAsDataURL(file);
}


// input
const FILE_INPUT = get_element('#add-message--files');
const FILE_LIST = []; // 
const PREVIEW_SHOWCASE = get_element('.p-form__preview-showcase')[0];

// inputFileListpreview
manipulate_msg_filelist = () => {
    // FileListpreview
    FILE_INPUT.addEventListener('change', (e) => {
        // FileList
        let files = e.target.files;
        // FileFileList
        let dt = new DataTransfer();

        // FileList
        if (files[0]) {
            // 
            if (!validate_format(files, ALLOWED_FILE_FORMAT)) {
                open_modal_with_closer_id('unavailable-format');

            // (10)
            } else if (FILE_LIST.length + files.length > 10) {
                open_modal_with_closer_id('too-many-uploads');

            // (25MB)
            } else if (!validate_size(files, FILE_LIST)) {
                open_modal_with_closer_id('too-large-data');

            } else {
                // datatransferfile_list
                for(let i = 0; i < FILE_LIST.length; i++ ){
                    dt.items.add(FILE_LIST[i]);
                }
                // datatransfer
                // FILE_LIST
                for(let i = 0; i < files.length; i++){
                    dt.items.add(files[i]);
                    FILE_LIST.push(files[i]);
                    // previewPREVIEW_LIST
                    insert_file_preview(files[i]);
                }
            }

            // dataransferFileListinput 
            e.target.files = dt.files;

            // preview  => 
            PREVIEW_SHOWCASE.classList.remove('u-dn');
        }
    })
}
if (FILE_INPUT) manipulate_msg_filelist()


// preview => preview (htmlonclick)
dump_file_preview = (dumper) => {
    let dumpers = get_element('.p-form__file-dumper');
    let dumper_idx = dumpers.indexOf(dumper);
    let file_blocks = get_element('.p-form__file-block');
    // previewli
    file_blocks[dumper_idx].parentNode.remove();
    // FILE_LIST
    FILE_LIST.splice(dumper_idx, 1);
    // dataransferFILE_LISTFileListinput
    let dt = new DataTransfer();
    for(let i = 0; i < FILE_LIST.length; i++ ){
        dt.items.add(FILE_LIST[i]);
    }
    FILE_INPUT.files = dt.files;
    // previewpreview
    if (!FILE_LIST[0]) PREVIEW_SHOWCASE.classList += ' u-dn';
}


// Preview ( ; ajax.js)
clear_msg_file_input = () => {
    FILE_INPUT.value = '';
    FILE_LIST.splice(0, FILE_LIST.length);
    PREVIEW_LIST.innerHTML = '';
    if (!FILE_LIST[0]) PREVIEW_SHOWCASE.classList += ' u-dn';
}
